.choice {
	display: flex;
	flex-direction: column;
	gap: 0.6em;
	padding-inline: min(0.75em, 1vw);
	margin-block: var(--block-spacing);

	p {
		margin-block: 0;
		padding: 0.05em 0.75em;
		border: var(--border-width) solid var(--text);
		background-color: var(--bg);
		color: white;
		text-align: center;
		font-size: 1.4em;

		&.selected { background-color: var(--text) }
	}
}